// theme colors
$bodyBackgroundColor: #101219;
$bodyHeaderColor: white;
$bodyTextColor: white;
$colorGold: #d49359;

// type
$typefaceSerif: "Lora", serif;
$typefaceSans: "Noto Sans Display", sans-serif;

h2 {
  line-height: 1.6em !important;
}

h3 {
  line-height: 1.3em !important;
  font-size: 1.1em;
  font-weight: 800;
  letter-spacing: 0.1em;
}

h4 {
  line-height: 1.6em !important;
  letter-spacing: 0.1em;
}

h5 {
  letter-spacing: 0.2rem;
}

h1,h2,h3,h4,h5,h6 {
  font-family: $typefaceSans;
  color: white !important;
}

.nav-link.active {
  color: white !important;
  border-bottom: 2px;
  border-color:#f9b275;
  border-bottom-style: solid;
}

.nav-link a:hover {
  color: white !important;
}

li {
  color: white;
  list-style-type: none;
  font-family: $typefaceSerif;
  font-size: 1.3rem;
}

nav li{
  font-family: $typefaceSans;
  font-size: 0.9rem;
  text-transform: uppercase;
}

p {
  color: white;
  font-size: 1.3rem;
  font-family: $typefaceSerif;
  line-height: 1.6em;
}

.img-border {
  outline: 4px solid #101219;
  outline-offset: -10px;
}

.theme-gold, a.active {
  color: $colorGold !important;
}

@-webkit-keyframes arrow-jump-right {
  0%   { opacity: 0;}
  100% { opacity: 1; 
        -webkit-transform: translateX(-6rem);
        -moz-transform: translateX(-6rem);
        transform: translateX(-6rem);}
}
svg #arrow-right:hover {
  -webkit-animation: arrow-jump-right 2s infinite; /* Safari 4+ */
  -moz-animation:    arrow-jump-right 2s infinite; /* Fx 5+ */
  -o-animation:      arrow-jump-right 2s infinite; /* Opera 12+ */
  animation:         arrow-jump-right 2s infinite; /* IE 10+, Fx 29+ */
}

@-webkit-keyframes arrow-jump-left {
  0%   { opacity: 0;}
  100% { opacity: 1; 
        -webkit-transform: translateX(-6rem);
        -moz-transform: translateX(-6rem);
        transform: translateX(-6rem);}
}
svg#arrow-left:hover {
  -webkit-animation: arrow-jump-left 2s infinite; /* Safari 4+ */
  -moz-animation:    arrow-jump-left 2s infinite; /* Fx 5+ */
  -o-animation:      arrow-jump-left 2s infinite; /* Opera 12+ */
  animation:         arrow-jump-left 2s infinite; /* IE 10+, Fx 29+ */
}

input {
  font-family: $typefaceSerif !important;
  font-size: 1.2em !important;
  border-radius: 8px;
  background-color: white;
  min-height: 1.6rem;
  padding: 0.8rem;
  width: 50%;
  border: 0;
}

textarea {
  font-family: $typefaceSerif !important;
  background-color: white;
  border-radius: 8px;
  font-size: 1.2em !important;
  min-height: 1.6rem;
  padding: 0.8rem;
  width: 100%;
  border: 0;
}

button {
  font-family: $typefaceSans !important;
}
label {
  display: inline-block;
  width: 100%;
}

.navbar {
  background-color: $bodyBackgroundColor;
}

.navbar li a:hover {
  color: #f9b275 !important;
}

a {
  color: #f9b275 !important;
}

div#carouselMain {
  margin-top: 240px;
}
section#footer {
  background-color: black;
}
section#content {
  background-color: $bodyBackgroundColor;
}

/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box;
  background-color: $bodyBackgroundColor;
  color: white;
}

p {
  color: white;
}

body {
  font-family: $typefaceSans;
}

.navbar-nav {
  align-items: center;
}
.navbar .navbar-nav .nav-link {
  color: #fff;
  font-size: 1.1em;
  letter-spacing: 0.2em;
}

/**
 * Make all elements from the DOM inherit from the parent box-sizing
 * Since `*` has a specificity of 0, it does not override the `html` value
 * making all elements inheriting from the root box-sizing value
 * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */
*, ::before, ::after {
  box-sizing: inherit;
} 
